.membersPopup-content {
    margin: auto;
    background: rgb(255, 255, 255);
    width: 50%;
    padding: 5px;
}

.membersPopup-arrow {
    color: rgb(48, 41, 41);
}

.membersPopup-overlay {
    background: rgba(0, 0, 0, 0.712);
}

.modal {
    font-size: 12px;
}

.modal .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
}

.modal .content {
    width: 100%;
    padding: 10px 5px;
}
.modal .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
}

.modal .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 50%;
    border: 1px solid #cfcece;
    outline:none;
}
.profileBody {
    color:white;
